"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import ProductCard from "@/app/components/After_log_homepage/ProductCard";


interface Product {
  id: string;
  name: string;
  image: string;
  price: string;
  description: string;
  category: string;
  subcategory: string; 
}

async function fetchProducts(category: string): Promise<Product[]> {
  try {
    const res = await fetch(`http://localhost:3000/api/product?category=${category}`);
    if (!res.ok) throw new Error("Failed to fetch products");
    return await res.json();
  } catch (error) {
    console.error("Error fetching products:", error);
    return [];
  }
}

export default function CategoryPage() {
  const params = useParams<{ category: string }>();
  const category = params?.category || "";
  const [products, setProducts] = useState<Product[]>([]);

  useEffect(() => {
    if (category) {
      fetchProducts(category).then(setProducts).catch(console.error);
    }
  }, [category]);

  return (
    <div className="container mx-auto p-6">
      <h1 className="text-3xl font-bold">Category: {category}</h1>
      
      {products.length === 0 ? (
        <p className="text-gray-500">No products found in this category.</p>
      ) : (
        <div className="grid grid-cols-3 gap-6 mt-6">
          {products.map((product) => (
            <ProductCard key={product.id} product={product} /> // âœ… ProductCard used here
          ))}
        </div>
      )}
    </div>
  );
}

